.chatScreen_text {
    margin-left: 10px;
    background-color: lightgray;
    padding: 15px;
    border-radius: 20px;

}
.chatScreen_timestamp{
    text-align: center;
    padding: 20px;
    color: gray;
}



.chatScreen_message{
    display: flex;
    align-items: center;
    padding: 20px;
}

.chatScreen_textUser {
    margin-left: auto;
    background-color: #29b3cd;
    padding: 15px;
    border-radius: 20px;
    color: white;

}
a{
    text-decoration: none;
    color: inherit;
}

.chatScreen_Input{
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width:100%;
    border-top: 1px solid #f9f9f9;


}

.chatScreen_InputField{
    flex:1;
    padding: 10px;
    border: none;
}

.chatScreen_Send{
    border: none;
    margin-right: 20px;
    background-color: white;
    font-weight: bold;
    color: #fe3d71;
}